.calculator {
  width: 300px;
  height: 400px;
  margin: 50px auto;
  padding: 50px 15px 20px 15px;
  display: grid;
  grid-template-areas:
    "display display display display"
    "clear clear divide multiply"
    "seven eight nine subtract"
    "four five six add"
    "one two three equals"
    "zero zero decimal equals";
  grid-gap: 10px 10px;
  background-color: rgb(191, 206, 227);
  border-radius: 20px;
  font-size: 30px;
}

$components: display, clear, add, subtract, multiply, divide, one, two, three,
  four, five, six, seven, eight, nine, zero, decimal, equals;

@each $component in $components {
  ##{$component} {
    grid-area: $component;
  }
}

#display {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  background-color: rgb(148, 168, 141);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4) inset;
  text-align: right;
  padding: 5px;
}

.calc-btn {
  background-color: #eee;
  border-radius: 10px;
  background-image: linear-gradient(to bottom, rgba(255,255,255,1), rgba(255,255,255, 0))
}

#clear {
  background-color: red;
  color: white;
  background-image: none;
}

.calc-btn:active {
  background-color: yellow;
}

.calc-btn:focus {
  outline: none;
}
